$font-family-base: 'Helvetica Neue', Helvetica, Arial, sans-serif;
$font-family-headings: 'Georgia', serif;
$link-color: #5D4037; 
$link-hover-color: #607D8B;

html, body {
    height: 100%;
    display: flex;
    flex-direction: column;
}

body, button, input, select, textarea {
  font-family: $font-family-base;
  font-size: 18px; 
  line-height: 1.5;
  color: #333;
  background-color: rgb(250, 250, 250);
}

.container {
    flex-grow: 1;
    max-width: 1800px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 40px;
    padding-left: 40px;
    padding-top: 40px;
    padding-bottom: 80px;
  }


h1, h2, h3, h4, h5, h6 {
    padding-top: 20px;
  font-family: $font-family-headings;
  font-weight: 700;
  color: #333;
  margin-top: 0;
}

h1 {
  font-size: 2.5em; 
  line-height: 2;
}

h2 {
  font-size: 2.25em;
  line-height: 2;
}

h3 {
  font-size: 2em;
  line-height: 1.3;
}

h4 {
  font-size: 1.75em;
}

h5 {
  font-size: 1.5em;
}

h6 {
  font-size: 1.25em;
}


a {
  color: $link-color;
  text-decoration: none;
  
  &:hover {
    color: $link-hover-color;
    text-decoration: none; 
  }
}

nav ul li a {
  color: $link-color;
  
  &:hover {
    color: $link-hover-color;
  }
}
